@import "./../Main.scss";

.wrapper{
    position: absolute;
    width: 100vw;
    height: 250vh;
    background-color: $backgroundColor;
}

.image{
    position: relative;
    min-height: 200px;
    min-width: 200px;
    background-position: center;
    background-size:contain;
    background-repeat: no-repeat;
}
.contactBackDrop{
    position: absolute;
    width: 100vw;
    height: 35vh;
    bottom: 0;
    display: flex;
    align-items:center;
    justify-content: space-between;
    // flex-direction: column;
    background-color: $contactBackDropColor;
}
.carouselWrapper{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding:10vh 10vw;
    // height: 100vh;
    width: 80vw;
    background-color: $backgroundColor;
}
