@import "../Main.scss";
.wrapper{
    position: relative;
    width: 210px;
    height: 170px;
    display: flex;
    align-items: center;
    justify-content: center;
   
}
.image{
    position: relative;
    width: 100%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 1;
    cursor: pointer;

}

.icon{
    position: relative;
    width: 50px;
    height: 50px;
    opacity: 0.5;
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: contain;
    background-color: $BrightColor;
    z-index: 2;
    transition: opacity 0.5s ease;
}
.text{
    position: absolute;
    font-family: "Amiri-Italic";
    color: white;
    font-size: 12pt;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: opacity 0.5s ease;

}
.hidden{
    opacity: 0;
}
.reveale{
    opacity: 1;
}