@import "../Main.scss";

.backDrop{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($backgroundColor, 0.9);
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content:center;

}



.previousButton{
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    left: 0;
    margin: 40px;
    z-index: 4;
    transform: translateY(-50%);
    cursor: pointer;
    background-color: $BrightColor;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }
}
.nextButton{
    position: absolute;
    width: 50px;
    height: 50px;
    top: 50%;
    right: 0;
    z-index: 4;
    transform: translateY(-50%);
    margin: 40px;
    cursor: pointer;
    background-color: $BrightColor;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }
}

.exitButton{
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 40px;
    top: 0;
    right: 0;
    background-color: $BrightColor;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    cursor: pointer;
    opacity: 0.5;
    &:hover{
        opacity: 1;
    }

}