@import "./../Main.scss";

.home{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 250vh;
    background-color: $backgroundColor;
}


.wrapper{
    position: absolute;
    width: 50%;
    height: 80vh;
    top: 65%;
    left: 50%;
    transform: translate(-50% ,-50%);
    display: flex;
    justify-content:space-between;
    align-items: center;
    flex-direction: column;

}

.inputNames{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.massege{
    position: relative;
    width: 100%;
    height: 160px;
    font-family: "Assistant-Light";
    font-size:22pt ;
    color: $backgroundColor;
}

.button{
    height: 50px;
    background-color: transparent;
    color: $BrightColor;
    padding: 0 40px;
    cursor: pointer;
    border: $BrightColor solid 2px; 
    border-radius: 25px;
    font-family: "Assistant-Bold";
    font-size: 12pt;
    &:hover {
        background-color: rgba($BrightColor,0.5);

    }
}


.contactBackDrop{
    position: absolute;
    width: 100vw;
    height: 35vh;
    bottom: 0;
    display: flex;
    align-items:center;
    justify-content: space-between;
    // flex-direction: column;
    background-color: $contactBackDropColor;
}

